.Input{
    width:100%;
    padding: 10px;
    box-sizing: border-box;

}

.Label{
    font-weight: bolder;
    display: block;
    margin-bottom: 8px ;

}

.InputElement{
    outline: none;
    border: 1px solid #ccc ;
    background-color: #fff;
    font:inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;

}
.InputElement:focus{
    outline: none;
    background-color: #CCC;

}
.Invalid{
    border: 1px solid red;
    background-color: #ffb075;
}